@charset 'utf-8';
@import url(common.css);

@media screen and (max-width: 480px) {
 
    .mobile-exposure {
        display: block;
    }
    .mobile-hide {
        display: none;
    }

    /* 최상단으로 가는 버튼 */
    .top-btn {
        width: 20vw;
        height: 20vw;
    }
    .top-btn:active {
        width: 19vw;
        height: 19vw;
    }

    .top-btn svg {
        width: 3.75vw;
        height: 5.4vw;
    }

    /* header */

    #header {
        width: 100%;
        height: 70px;
        position: fixed;
        z-index: 999;
    }

    #header .header-left .logo {
        font-family: 'GmarketSansBold';
        font-size: 20px;
    }

    #header .header-right {
        display: flex;
        align-items: center;
    }

    /* 헤더 시간 */
    #header .header-left .info {
        opacity: 0;
        transition: .1s;
    }

    /* 헤더 라이트모드 & 다크모드 버튼 */
    #header .light-dark-mode {
        display: block;
        width: 55px;
        height: 25px;
        border-radius: 35px;
        background-color: #FF9922;
        display: flex;
        align-items: center;
    }
    #header .light-dark-mode .mode-btn {
        width: 17px;
        height: 17px;
        border-radius: 17px;
        background-color: #fff;
        margin-left: 4px;
        position: relative;
    }

    #header .menu-btn {
        margin-left: 25px;
    }

    /* intro section =========== Start Line ====================================== */
    /* 라이트모드 & 다크모드 버튼 */
    #intro .light-dark-mode {
        top: -50vh;
    }

    /* 메인 title */
    #intro .center-title h1 {
        width: 250px;
        display: inline-block;
        font-size: 65px;
        line-height: 60px;
        letter-spacing: -1px;
        font-family: 'GmarketSansBold';
    }
    #intro .center-title h1 span {
        font-size: 55px;
        font-family: 'GmarketSansBold';
    }
    
    #intro .center-title .greeting-ment {
        font-size: 1rem;
        letter-spacing: -0.2px;
    }

    /* intro에 고정된 메인 도형 */
    #intro .signature-shape {
        position: absolute;
        top: 55vh;
        left: -37vw;
        width: 45vh;
        height: 45vh;
        animation: rotate-shape 20s linear infinite;
    }

    #intro .signature-shape .line {
        height: 1px;
    }
    /* intro section ======================================= End Line ========== */

    /* about section =========== Start Line ====================================== */
    #about {
        height: 100vh;
    }

    #about .about-scroll-option > .swiper-wrapper {
        scroll-snap-type: y mandatory;
        scroll-behavior: smooth;
    }

    #about .about-scroll-option > .swiper-wrapper,
    #about .about-scroll-option > .swiper-wrapper > .swiper-slide {
        height: auto;
    }

    #about .about-container {
        width: 83vw;
        flex-direction: column;
        padding: 15vh 0;
    }

    #about .about-left {
        position: relative;
    }
    #about .about-left .title h2 {
        font-size: 7vw;
        font-weight: 500;
        letter-spacing: -2px;
    }
    #about .about-left .title p {
        font-size: 7vw;
        font-weight: 500;
    }

    #about .about-left .introduction {
        width: 100%;
        font-size: 4vw;
        letter-spacing: -0.5px;
        margin-top: 0.9vw;
    }

    #about .about-left .keywords {
        margin: 4vw 0 4vw;
    }
    #about .about-left .keywords p {
        font-size: 2.6vw;
        padding: 2.7vw 2.5vw 2.2vw;
    }
    #about .about-left .keywords p:not(:first-child) {
        margin-left: 1.7vw;
    }

    #about .about-left .swiper-navigation {
        position: absolute;
        /* right: 0; */
        /* bottom: -1020%; */
    }

    #about .about-right {
        width: 100%;
        flex-direction: column;
        align-items: flex-end;
    }

    #about .about-right .about-title, #about .about-right .about-contents  {
        width: 100%;
        height: 107vw;
        margin-top: 4.16vw;
    }
    #about .about-right .about-contents {
        margin-left: 0;
    }

    #about .about-right > .swiper-navigation {
        width: 25vw;
        height: 10vw;
    }
    #about .about-right > .swiper-navigation .navi-btn .page-number {
        font-size: 3.5vw;
        letter-spacing: -0.3vw;
    }

    /* about-1)info */

    #about .about-right .about-contents .content-1-info h3 {
        font-size: 6.25vw;
    }
    #about .about-right .about-contents .content-1-info p {
        font-size: 3.3vw;
        line-height: 5.2vw;
        margin-top: 6vw;
    }

    /* about-2)skill */

    #about .about-right .about-title .title-2-skill {
        padding-left: 8vw;
    }

    #about .about-right .about-title .title-2-skill h3 {
        font-size: 20.8vw;
        letter-spacing: -0.5vw;
    }

    #about .about-right .about-contents .content-2-skill h3 {
        font-size: 6.25vw;
    }

    #about .about-right .about-contents .content-2-skill .tool-skill {
        margin-top: 14.5vw;
    }

    #about .about-right .about-contents .content-2-skill .icon {
        height: 11.5vw;
        margin-top: 6.2vw;
    }
    #about .about-right .about-contents .content-2-skill .icon img:not(:last-child) {
        margin-right: 3.7vw;
    }

    /* about-3)aspirations */

    #about .about-right .about-title .title-3-aspi .title-3-container {
        margin-bottom: 12.08vw;
    }

    #about .about-right .about-title .title-3-aspi .if-title {
        font-size: 31.25vw;
        letter-spacing: -0.5vw;
        margin-left: -2vw;
    }

    #about .about-right .about-title .title-3-aspi .if-branch-text {
        font-size: 5.7vw;
        font-weight: 500;
        letter-spacing: -0.03vw;
    }

    #about .about-right .about-title .title-3-aspi .parentheses {
        font-size: 8.33vw;
        color: #9B9B9B;
        bottom: 31vw;
    }
    #about .about-right .about-title .title-3-aspi .parentheses .space {
        width: 60vw;
    }

    #about .about-right .about-title .title-3-aspi .brace {
        font-size: 8.33vw;
        bottom: 8.33vw;
        right: 8.33vw;
    }

    #about .about-right .about-contents .content-3-aspi .subtitle {
        font-size: 3.1vw;
        margin-bottom: 3.4vw;
    }
    #about .about-right .about-contents .content-3-aspi .content {
        font-size: 2.9vw;
        line-height: 4.6vw;
    }

    /* about-4)motto */

    #about .about-right .about-title .title-4-motto p {
        font-size: 11vw;
        letter-spacing: -0.15vw;
    }

    #about .about-right .about-contents .content-4-motto p {
        font-size: 2.9vw;
        line-height: 4.6vw;
    }
    /* about section ======================================= End Line ========== */

    /* project section =========== Start Line ====================================== */
    #project .signature-shape {
        position: absolute;
        top: 2.14vw;
        left: calc(50% - 26vw);
        z-index: 0;
        width: 52vw;
        height: 52vw;
        animation: rotate-shape 20s linear infinite;
        transition: .2s ease-in;
    }
    /* project section ======================================= End Line ========== */

    /* contact section =========== Start Line ====================================== */
    #contact .contact-container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    #contact h2 {
        font-size: 9.79vw;
        letter-spacing: -0.2vw;
    }

    #contact p {
        font-size: 3.33vw;
        margin: 1.4vw 0 10vw;
    }

    #contact .chat-link {
        width: 27.9vw;
        height: 9.37vw;
    }
    
    #contact .chat-link span {
        font-size: 3.54vw;
    }
    
    #contact .chat-link svg {
        margin-left: 2.08vw;
    }
    /* contact section ======================================= End Line ========== */
}
