@charset 'utf-8';
@import url(common.css);
/* 공동 style */

@keyframes rotate-shape {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes textShake {
    0%, 40% {
        transform: rotate(0deg);
    }
    55%{
        transform: rotate(-3deg);
    }
    60% {
        transform: rotate(5deg);
    }
    80%, 100% {
        transform: rotate(0deg);
    }
}

@keyframes textJump {
    0% {
        transform: translateY(0);
    }
    20% {
        transform: translateY(-0.6vw);
    }
    30% {
        transform: translateY(0.2vw);
    }
    45% {
        transform: translateY(-0.1vw);
    }
    60% {
        transform: translateY(0.1vw);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes oooops {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

:root {
    --main-color: #F97904;
    --sub-color: #FF9922;

    --black-color: #222;
    --grey-color: #888;
    --grey-color2: #777;

    --top-btn-color: rgba(255, 192, 72, 0.5);
    --top-btn-hover-color: rgba(255, 192, 72, 0.8);
}


.tablet-exposure,
.mobile-exposure {
    display: none;
}

#container {
    width: 100%;
    height: 100%;
}

/* 최상단으로 가는 버튼 */
.top-btn {
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 999;
    width: 5.5vw;
    height: 5.5vw;
    border: 0;
    border-top-left-radius: 10px;
    background-color: var(--top-btn-color);
    /* background-color: #FFC148; */
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .2s;
    cursor: pointer;
}
#container > .swiper > .swiper-pagination {
    display: none !important;
}

.top-btn:hover {
    background-color: var(--top-btn-hover-color);
}
.top-btn:active {
    width: 5vw;
    height: 5vw;
}

.top-btn svg {
    width: 0.9vw;
    height: 1.35vw;
}

/* header =========== Start Line ====================================== */

#header {
    width: 100%;
    height: 14.5vh;
    /* 140px */
    position: fixed;
    z-index: 999;
}

#header .header-container {
    width: 90vw;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* 헤더 왼쪽 logo / info */
#header .header-left {
    display: flex;
    position: relative;
}

#header .header-left .logo {
    font-family: 'GmarketSansBold';
    font-size: 30px;
    color: var(--black-color);
}

#header .header-left .info {
    position: absolute;
    top: 50%;
    left: 12vw;
    transform: translate(0, -50%);
    width: 20vw;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #A8A8A8;
    font-size: 0.7rem;
    font-family: 'GmarketSansLight';
}

#header .header-left .info span {
    margin: 0 1vw;
}

/* 헤더 시간 */
#header .time-check {
    font-size: 15px;
    color: var(--black-color);
}

/* 헤더 라이트모드 & 다크모드 버튼 */
#header .light-dark-mode {
    display: none;
}

/* 헤더 메뉴 */
#header .menu-btn {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    height: 15px;
    cursor: pointer;
    margin-left: 72.56px;
}

#header .menu-btn span {
    display: inline-block;
    width: 25px;
    height: 2px;
    background-color: var(--black-color);
}

#contents-swiper {
    width: 100%;
    height: 100vh;
}

/* header ======================================= End Line ========== */

/* footer-fixed =========== Start Line ====================================== */
#footer-fixed {
    position: fixed;
    left: 5vw;
    bottom: 4vw;
    z-index: 999;
}

#footer-fixed .github-link {
    display: inline-block;
    width: 35px;
    height: 35px;
    background-image: url('./style-assets/images/github.png');
    margin-bottom: 3vh;
    opacity: .4;
}

#footer-fixed p {
    font-size: 0.8vw;
    font-weight: 300;
    letter-spacing: -0.0vw;
    color: var(--grey-color);
    font-family: 'Noto Sans KR', sans-serif;
}

/* footer-fixed ====================================== End Line ===========*/

/* intro section =========== Start Line ====================================== */
#intro {
    width: 100%;
    height: 100%;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

#intro .center-title {
    z-index: 1;
}

#intro .center-title h1 {
    width: 250px;
    display: inline-block;
    font-size: 85px;
    line-height: 80px;
    letter-spacing: -1px;
    font-family: 'GmarketSansBold';
}

#intro .center-title h1 span {
    font-size: 70px;
    font-family: 'GmarketSansBold';
}

#intro .center-title .greeting-ment {
    font-size: 1.2rem;
    letter-spacing: -0.2px;
}

/* 라이트모드 & 다크모드 버튼 */
#intro .light-dark-mode {
    position: absolute;
    top: 60.5vh;
    right: 16vw;
    width: 35px;
    height: 90px;
    border-radius: 35px;
    background-color: #FF9922;
    display: flex;
    justify-content: center;
    transition: 1s ease;
    cursor: pointer;
}

#intro .light-dark-mode .mode-btn {
    width: 25px;
    height: 25px;
    border-radius: 25px;
    background-color: #fff;
    position: relative;
    transition: all .2s ease;
}

#intro .light-dark-mode .mode-btn::before {
    content: '';
    border-left: 1px solid #D9D9D9;
    position: absolute;
    top: -100vh;
    left: 50%;
    transform: translate(-50%, 0);
    height: 100vh;
}

/* intro에 고정된 메인 도형 */
#intro .signature-shape {
    position: absolute;
    top: 50vh;
    left: -15vh;
    z-index: 0;
    width: 60vh;
    height: 60vh;
    animation: rotate-shape 20s linear infinite;
    transition: .2s ease-in;
}

.signature-shape .line {
    position: absolute;
    top: 50%;
    width: 100%;
    height: 1.5px;
    background-color: #FF9922;
    transform-origin: center;
}

.signature-shape .line-1 {
    transform: rotate(90deg);
}

.signature-shape .line-2 {
    transform: rotate(105deg);
}

.signature-shape .line-3 {
    transform: rotate(120deg);
}

.signature-shape .line-4 {
    transform: rotate(135deg);
}

.signature-shape .line-5 {
    transform: rotate(150deg);
}

.signature-shape .line-6 {
    transform: rotate(165deg);
}

.signature-shape .line-7 {
    height: 2px;
    transform: rotate(0deg);
}

.signature-shape .line-8 {
    transform: rotate(-165deg);
}

.signature-shape .line-9 {
    transform: rotate(-150deg);
}

.signature-shape .line-10 {
    transform: rotate(-135deg);
}

.signature-shape .line-11 {
    transform: rotate(-120deg);
}

.signature-shape .line-12 {
    transform: rotate(-105deg);
}

/* intro section ======================================= End Line ========== */

/* about section =========== Start Line ====================================== */
#about {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    font-family: 'Noto Sans KR', sans-serif;
}

#about .about-scroll-option {
    width: 100%;
    height: 100%;
}

#about .about-scroll-option>.swiper-wrapper>.swiper-slide {
    display: flex;
    align-items: center;
}

#about .about-container {
    display: flex;
    justify-content: space-between;
    width: 70vw;
    margin: 0 auto;
}

#about .about-left .title {
    display: flex;
    align-items: flex-start;
    margin-top: -13px;
}

#about .about-left .title h2 {
    font-size: 2.65vw;
    font-weight: 500;
    letter-spacing: -2px;
}

#about .about-left .title p {
    font-size: 2.65vw;
    font-weight: 500;
    transform-origin: bottom center;
    cursor: pointer;
    transform-origin: bottom center;
}
#about .about-left .title p.shake-text {
    animation: textShake 1.7s linear infinite;
}
#about .about-left .title p.jump-text {
    animation: textJump 0.9s ease;
}

#about .about-left .title span {
    display: inline-block;
    font-size: 0.8vw;
    font-weight: 100;
    color: var(--sub-color);
    opacity: 0;
}
#about .about-left .title span.oooops {
    animation: oooops 1.2s linear;
}

#about .about-left .introduction {
    width: 18vw;
    font-size: 1.3vw;
    letter-spacing: -0.5px;
    margin-top: 20px;
}

#about .about-left .keywords {
    width: 100%;
    display: flex;
    font-family: 'GmarketSansLight';
    font-weight: 700;
    margin-top: 30px;
}

#about .about-left .keywords p {
    font-size: 0.6vw;
    line-height: 0.6vw;
    border: 1px solid var(--main-color);
    padding: 0.55vw 0.65vw 0.4vw;
    border-radius: 100px;
    color: var(--main-color);
}

#about .about-left .keywords p:not(:first-child) {
    margin-left: 0.6vw;
}

#about .swiper-navigation {
    width: 6.5vw;
    height: 2vw;
}

#about .swiper-navigation .navi-btn {
    width: 100%;
    height: 100%;
    margin-top: 1.8vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#about .swiper-navigation .navi-btn svg {
    cursor: pointer;
}

#about .swiper-navigation .page-number {
    text-align: center;
    letter-spacing: -0.1vw;
    font-size: 0.9vw;
    font-weight: 300;
    color: var(--grey-color2);
}

#about .about-right {
    display: flex;
}

#about .about-right .about-title,
#about .about-right .about-contents {
    width: 22vw;
    height: 29vw;
    /* width: 430px;
    height: 555px; */
    border-radius: 10px;
    background-color: #F6F6F6;
}

#about .about-right .about-contents {
    margin-left: 1.3rem;
}

/* about-1)info */

#about .about-right .about-title .swiper-slide .title-1-info {
    width: 100%;
    height: 100%;
}

#about .about-right .about-contents .content-1-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#about .about-right .about-contents .content-1-info h3 {
    font-size: 1.55vw;
    font-weight: 600;
    letter-spacing: -0.05vw;
}

#about .about-right .about-contents .content-1-info p {
    font-size: 0.85vw;
    line-height: 1.4vw;
    margin-top: 1.3vw;
}

/* about-2)skill */

#about .about-right .about-title .title-2-skill {
    display: flex;
    align-items: center;
    padding-left: 2.6vw;
}

#about .about-right .about-title .title-2-skill h3 {
    font-size: 5.2vw;
    letter-spacing: -0.2vw;
    font-weight: 500;
}

#about .about-right .about-contents .content-2-skill {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

#about .about-right .about-contents .content-2-skill h3 {
    font-size: 1.6vw;
    font-weight: normal;
    color: #969696;
}

#about .about-right .about-contents .content-2-skill .tool-skill {
    margin-top: 3.6vw;
}

#about .about-right .about-contents .content-2-skill .icon {
    width: 100%;
    height: 3vw;
    margin-top: 1.5vw;
    display: flex;
}

#about .about-right .about-contents .content-2-skill .icon img {
    width: 100%;
    height: 100%;
}

#about .about-right .about-contents .content-2-skill .icon img:not(:last-child) {
    margin-right: 1vw;
}

/* about-3)aspirations */

#about .about-right .about-title .title-3-aspi {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

#about .about-right .about-title .title-3-aspi .title-3-container {
    margin-bottom: 3vw;
}

#about .about-right .about-title .title-3-aspi .if-title {
    font-size: 7.8vw;
    font-weight: 500;
    letter-spacing: -0.2vw;
    margin-left: -0.5vw;
}

#about .about-right .about-title .title-3-aspi .if-branch-text {
    font-size: 1.5vw;
    font-weight: 500;
    letter-spacing: -0.03vw;
}

#about .about-right .about-title .title-3-aspi .parentheses,
#about .about-right .about-title .title-3-aspi .brace {
    position: absolute;
}

#about .about-right .about-title .title-3-aspi .parentheses {
    display: flex;
    font-size: 2vw;
    font-weight: 500;
    color: #9B9B9B;
    bottom: 8.85vw;
}

#about .about-right .about-title .title-3-aspi .parentheses .space {
    width: 16.8vw;
}

#about .about-right .about-title .title-3-aspi .brace {
    font-size: 2vw;
    letter-spacing: -0.1vw;
    color: #E7E7E7;
    bottom: 2vw;
    right: 2vw;
}

#about .about-right .about-contents .content-3-aspi {
    display: flex;
    justify-content: center;
    align-items: center;
}

#about .about-right .about-contents .content-3-aspi .subtitle {
    font-size: 0.9vw;
    color: #9B9B9B;
    margin-bottom: 1.8vw;
}

#about .about-right .about-contents .content-3-aspi .content {
    font-size: 0.8vw;
    line-height: 1.33vw;
}


/* about-4)motto */

#about .about-right .about-title .title-4-motto {
    display: flex;
    justify-content: center;
    align-items: center;
}

#about .about-right .about-title .title-4-motto p {
    font-size: 3.1vw;
    font-weight: 500;
    letter-spacing: -0.07vw;
}

#about .about-right .about-title .title-4-motto p.impossible {
    color: #146FB0;
}

#about .about-right .about-title .title-4-motto p.possible {
    color: #DF6106;
}

#about .about-right .about-title .title-4-motto p.possible span {
    color: #000;
}

#about .about-right .about-contents .content-4-motto {
    display: flex;
    justify-content: center;
    align-items: center;
}

#about .about-right .about-contents .content-4-motto p {
    font-size: 0.8vw;
    line-height: 1.33vw;
}

#about .about-right .about-contents .content-4-motto p span {
    font-weight: 600;
}

/* about section ======================================= End Line ========== */

/* project section =========== Start Line ====================================== */
#project {
    background-color: transparent;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
}

#project .signature-shape {
    position: absolute;
    top: 1.14vw;
    left: 11.87vw;
    z-index: 0;
    width: 30vh;
    height: 30vh;
    animation: rotate-shape 20s linear infinite;
    transition: .2s ease-in;
}

#project .signature-shape .line {
    height: 1px;
}

#project .signature-shape .line-7 {
    height: 0.8px;
}


/* project section ======================================= End Line ========== */



/* contact section =========== Start Line ====================================== */

#contact {
    background-image: url('./style-assets/images/contact-bg.png');
    font-family: 'Noto Sans KR', sans-serif;
    color: #fff;

    display: flex;
    align-items: center;
    padding-left: 18vw;
}

#contact h2 {
    font-size: 3.64vw;
    font-family: 'GmarketSansLight';
    font-weight: 100;
    letter-spacing: -0.05vw;
}

#contact p {
    font-size: 1.04vw;
    font-weight: 100;
    opacity: .65;
    margin: 0.5vw 0 2.7vw;
}

#contact .chat-link {
